const products = [
    { name: "Passenger Elevators", url: "./services.html#pass-ele" },
    { name: "Home Elevators", url: "./services.html#home-ele" },
    { name: "Freight Elevators", url: "./services.html#frei-ele" },
    { name: "Hospital Elevators", url: "./services.html#hos-ele" },
    { name: "Panoramic Elevators", url: "./services.html#pano-ele" },
    { name: "24/7 Emergency Support", url: "./services.html#24-ele" },
    { name: "Installation", url: "./services.html#enroll-section" },
    { name: "Modernization", url: "./services.html#enroll-section" },
    { name: "Service", url: "./services.html#enroll-section" },
    { name: "Maintenance", url: "./services.html#enroll-section" },
    { name: "Elevators", url: "./services.html" },
    { name: "Escalators", url: "./services.html" },
    { name: "Canny", url: "./services.html" },
    { name: "Mitsubishi", url: "./services.html" },
    // { name: "Smart ", url: "./services.html" },
];

const openSearch = document.getElementById("openSearch");
const closeSearch = document.getElementById("closeSearch");
const searchOverlay = document.getElementById("searchOverlay");
const searchInput = document.getElementById("searchInput");
const searchResults = document.getElementById("searchResults");

// Open search overlay
openSearch.addEventListener("click", () => {
    searchOverlay.style.opacity = "1";
    searchOverlay.style.pointerEvents = "auto";
    searchInput.focus();
});

// Close search overlay
closeSearch.addEventListener("click", closeSearchOverlay);
document.addEventListener("keydown", (e) => {
    if (e.key === "Escape") closeSearchOverlay();
});
document.addEventListener("click", (e) => {
    if (!searchOverlay.contains(e.target) && !openSearch.contains(e.target)) {
        closeSearchOverlay();
    }
});

// Function to close the search overlay
function closeSearchOverlay() {
    searchOverlay.style.opacity = "0";
    searchOverlay.style.pointerEvents = "none";
    searchResults.innerHTML = ""; // Clear results
}

searchInput.addEventListener("input", function () {
    const query = searchInput.value.toLowerCase();
    searchResults.innerHTML = ""; // Clear previous results

    if (query.trim() === "") {
        searchResults.style.display = "none";
        return;
    }

    // Filter matching products
    const filteredProducts = products.filter(product =>
        product.name.toLowerCase().includes(query)
    );

    if (filteredProducts.length === 0) {
        searchResults.innerHTML = "<p class='text-gray-400 text-center'>No results found</p>";
    } else {
        filteredProducts.forEach((product, index) => {
            const item = document.createElement("a");
            item.href = product.url;
            item.textContent = product.name;
            item.classList = "search-result-item block p-3 rounded mt-2 cursor-pointer hover:bg-gray-700 text-gray-400 transition text-center";

            // Delay animation for a smooth staggered effect
            item.style.animationDelay = `${index * 0.05}s`;

            searchResults.appendChild(item);
        });
    }

    searchResults.style.display = "block";
});


document.getElementById("search-btn").addEventListener("click", function () {
    document.querySelector(".nav_search-container").classList.add("active");
});

document.addEventListener("click", function (event) {
    let searchContainer = document.querySelector(".nav_search-container");
    if (!searchContainer.contains(event.target) && !event.target.matches("#search-btn")) {
        searchContainer.classList.remove("active");
    }
});



